<template>
  <div>
    <main-app-header :page-title="pageTitle" />
    <navigation-menu />
    <router-view />
  </div>
</template>

<script setup>
import MainAppHeader from '@/components/mainAppHeader'
import NavigationMenu from '@/views/device/component/navigationMenu'
import { reactive, computed } from 'vue'
import { useRouterStore } from '@/store/router'
import { fetchDeviceInfo } from '@/apis/device'
import { ElMessage } from 'element-plus'
import { useRoute } from 'vue-router'

const route = useRoute()
const pageTitle = computed(() => {
  return '设备列表 - ' + route.meta.title + '[' + deviceInfo.deviceName + ']'
})

const deviceInfo = reactive({
  deviceName: 'default_name'
})

const routerStore = useRouterStore()
fetchDeviceInfo({ productId: routerStore.productId, deviceId: routerStore.deviceId }).then(res => {
  if (res.code === 200) {
  //  请求成功
    deviceInfo.deviceName = res.data.deviceName
  }
}, rej => {
  ElMessage({
    type: 'error',
    message: '网络错误'
  })
})
</script>

