<template>
  <el-menu
    :default-active="activeMenu"
    class="el-menu-demo"
    mode="horizontal"
    :ellipsis="false"
    @select="handleSelect"
  >
    <el-menu-item v-for="item in navigationMenu" :key="item.name" :index="item.name">{{ item.meta.title }}</el-menu-item>
  </el-menu>
</template>

<script setup>
import { asyncRouters } from '@/router'
import { reactive, onMounted, computed } from 'vue'
import { useRouterStore } from '@/store/router'
import { useRouter } from 'vue-router'

const routerStore = useRouterStore()
const router = useRouter()
const navigationMenu = reactive([])
const activeMenu = computed(() => {
  return routerStore.subRouterName
})

onMounted(() => {
  getNavigationMenu()
})

function getNavigationMenu() {
  while (navigationMenu.length) {
    navigationMenu.pop()
  }
  navigationMenu.push(...(asyncRouters.find(item =>
    item.name === 'deviceLayout'
  ).children[0].children))
}
//  又bug！bug：刷新后menu丧失焦点
function handleSelect(index) {
  router.push({ name: index, params: { productId: routerStore.productId, deviceId: routerStore.deviceId }})
}
</script>

