import request from '@/utils/axios'

export function addDevice(data) {
  return request({
    url: '/api/data/addDevice',
    method: 'POST',
    data
  })
}

export function fetchDeviceList(params) {
  return request({
    url: '/api/data/fetchDeviceList',
    method: 'GET',
    params
  })
}

export function fetchDeviceInfo(params) {
  return request({
    url: '/api/data/fetchDeviceInfo',
    method: 'GET',
    params
  })
}

export function fetchDeviceStatus(data) {
  return request({
    url: '/api/data/fetchDeviceStatus',
    method: 'POST',
    data
  })
}

export function deviceDataUploadInfo(params) {
  return request({
    url: '/api/data/deviceDataUploadInfo',
    method: 'GET',
    params
  })
}

export function deleteDevice(params) {
  return request({
    url: '/api/data/deleteDevice',
    method: 'DELETE',
    params
  })
}

export function sendCommand(data) {
  return request({
    url: '/api/data/sendCommand',
    method: 'POST',
    data
  })
}

export function fetchCommandList(params) {
  return request({
    url: '/api/data/fetchCommandList',
    method: 'GET',
    params
  })
}

export function addApiKey(params) {
  return request({
    url: '/api/data/addApiKey',
    month: 'GET',
    params
  })
}
